import React, { useState } from 'react';
import './contactMe.css';
import ReactDOM from 'react-dom';
import { send } from 'emailjs-com';

function ContactMe(){
    //error message visibility 
    const [showError, toggleShowError] = React.useState(false);

    //succesful message visibility 
    const [showSuccess, toggleShowSuccess] = React.useState(false);

    //unsuccessful message visibility 
    const [showUnsuccess, toggleShowUnsuccess] = React.useState(false);

    const [toSend, setToSend] = useState({
        name: '',
        subject: '',
        message: '',
        email: '',
    });

    const onSubmit = (e) => {

        toggleShowError(false)
        toggleShowSuccess(false)
        toggleShowUnsuccess(false)

        e.preventDefault();
        if(
            toSend.name=="" ||
            toSend.email=="" ||
            toSend.subject=="" ||
            toSend.message=="" 
        ){
            //change error message to visible using react hooks
            //{show && <div> something </div>}
            toggleShowError(true)
        }else{
            send(
                'service_pf4uz18',
                'template_up1ls64',
                toSend,
                'user_HV4RJtiQROOKY7PeuyRj1'
              )
                .then((response) => {
                  console.log('SUCCESS!', response.status, response.text);
                  toggleShowError(false)
                  toggleShowSuccess(true)
                })
                .catch((err) => {
                  console.log('FAILED...', err);
                  toggleShowError(false)
                  toggleShowUnsuccess(true)
                });
        }
        
    };
    
    const handleChange = (e) => {
      setToSend({ ...toSend, [e.target.name]: e.target.value });
    };

    return(
        <div id='contact-section' className="contact-container">
            <div className="contact-inner-container">
                <h1 className="contact-me-heading">Contact Me</h1>
                <svg className="small-logo-svg" 
                xmlns="http://www.w3.org/2000/svg" 
                fills="none"
                width="400px" 
                height="400px" 
                viewBox="-0.5 -0.5 484 484" 
                >
                    <defs/>
                    <g>
                        <ellipse className="logo-ellipse" cx="242" cy="242" rx="240" ry="240" fill="none" stroke="#000000" stroke-width="4" pointer-events="all"/>
                        <path className="logo-lines" d="M 82 82 L 402 82" fill="none" stroke="#000000" stroke-width="4" stroke-miterlimit="10" pointer-events="stroke"/>
                        <path className="logo-lines" d="M 82 402 L 82 82" fill="none" stroke="#000000" stroke-width="4" stroke-miterlimit="10" pointer-events="stroke"/>
                        <path className="logo-lines" d="M 82 402 L 402 402" fill="none" stroke="#000000" stroke-width="4" stroke-miterlimit="10" pointer-events="stroke"/>
                        <path className="logo-lines" d="M 242 402 L 242 82" fill="none" stroke="#000000" stroke-width="4" stroke-miterlimit="10" pointer-events="stroke"/>
                        <path className="logo-lines" d="M 82 402 L 242 82" fill="none" stroke="#000000" stroke-width="4" stroke-miterlimit="10" pointer-events="stroke"/>
                        <path className="logo-lines" d="M 402 402 L 242 82" fill="none" stroke="#000000" stroke-width="4" stroke-miterlimit="10" pointer-events="stroke"/>
                        <path className="logo-lines" d="M 242 402 L 402 82" fill="none" stroke="#000000" stroke-width="4" stroke-miterlimit="10" pointer-events="stroke"/>
                        <path className="logo-lines" d="M 82 242 L 242 242" fill="none" stroke="#000000" stroke-width="4" stroke-miterlimit="10" pointer-events="stroke"/>
                    </g>
                </svg>
                <div className="contact-me-form-wrapper">
                    <p className="contact-text">
                        Hey, if you have any questions, requests or you simply
                        want to get to know me better don't hesitate to contact me 
                        using the form below!
                    </p>
                    <form onSubmit={onSubmit} className="form">
                        <input
                            className="form-name"
                            type='text'
                            name='name'
                            placeholder='Name'
                            value={toSend.name}
                            onChange={handleChange}
                        />
                        <input
                            className="form-email"
                            type='email'
                            name='email'
                            placeholder='Email'
                            value={toSend.email}
                            onChange={handleChange}
                        />
                        <input
                            className="form-subject"
                            type='text'
                            name='subject'
                            placeholder='Subject'
                            value={toSend.subject}
                            onChange={handleChange}
                        />
                        <textarea
                            className="form-message"
                            type='text'
                            name='message'
                            placeholder='Message'
                            value={toSend.message}
                            onChange={handleChange}
                        />
                        <button 
                        className="send-email-button"
                        type='submit'>Send
                        </button>
                        {showError && 
                        <p 
                        className="error-message">
                        Every field must be filled!</p>
                        }
                        {showSuccess && 
                        <p 
                        className="successful-message">
                        Email sent!</p>
                        }
                        {showUnsuccess && 
                        <p 
                        className="unsuccessful-message">
                        Error, email not sent!</p>
                        }
                    </form>
                </div>
            </div>
        </div>
    );
}


export default ContactMe;