import React from 'react';
import "./skills.css";
import android_logo from "../images/android_logo.png";
import java_logo from "../images/java_logo.png";
import css3_logo from "../images/css3_logo.png";
import html5_logo from "../images/html5_logo.png";
import javascript_logo from "../images/JavaScript_logo.png";
import mysql_logo from "../images/mysql_logo.png";
import python_logo from "../images/python_logo.png";
import reactjs_logo from "../images/reactjs_logo.png";
import springboot_logo from "../images/springboot_logo.png";
import angular_logo from "../images/angular_logo.png";

function Skills(){
    return(
        <div id='skills-section' className="skills-container">
            <div className="skills-inner-container">
                <h1 className="skills-heading">Skills</h1>
                <ul className="skills">
                    <li className="skill-card">
                        <img className="skill-img" src={android_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={java_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={springboot_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={reactjs_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={mysql_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={python_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={javascript_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={html5_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={css3_logo} />
                    </li>
                    <li className="skill-card">
                        <img className="skill-img" src={angular_logo} />
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default Skills;