import React from 'react';
import "./projectCards.css";

function ProjectCards(){
    return(
        <div id='projects-section' className="projects-container">
            <div className="projects-inner-container">
                <h1 className="projects-heading">
                    Projects
                </h1>
                <div className="cards-container">
                <ul className="cards">
                        <li className="card">
                            <h3 className="card-heading">
                                Premier League Manager System
                            </h3>
                            <button 
                            onClick={()=> window.open("https://github.com/AlexPeiris7/Premier-League-Manager-System", "_blank")}
                            className="github-button">
                                GitHub
                            </button>
                            <p className="card-text">
                                A premier league manager 2020/2021 system consisting of a
                                console application developed with java, Front-End
                                developed with the AngularJs framework and Back-end developed
                                with Java Spring Boot framework and with the use of OOP principles.
                            </p>
                            <p className="card-languages">
                                <span className="languages-sub">Languages:</span> 
                                <span className="language"> Java-65.7%</span>
                                <span className="language"> Type-Script_21.4%</span>
                                <span className="language"> HTML-4.0%</span>
                                <span className="language"> CSS-6.1%</span>
                                <span className="language"> JavaScript-2.8%</span>
                            </p>
                        </li>

                        <li className="card" >
                            <h3 className="card-heading">
                                Car Guessing Android Game
                            </h3>
                            <button 
                            onClick={()=> window.open("https://github.com/AlexPeiris7/Car-guessing-android-game", "_blank")}
                            className="github-button">
                                GitHub
                            </button>
                            <p className="card-text">
                                A car guessing Android game with 4 modes, custom difficulties
                                (Hard, Easy), custom countdown timer and custom number of rounds.
                            </p>
                            <p className="card-languages">
                                <span className="languages-sub">Languages: </span> 
                                <span className="language">Java-100.0%</span>
                            </p>
                            
                        </li>

                        <li className="card">
                            <h3 className="card-heading">
                                Personal Movie Database
                            </h3>
                            <button 
                            onClick={()=> window.open("https://github.com/AlexPeiris7/Personal-Movie-Database", "_blank")}
                            className="github-button">
                                GitHub
                            </button>
                            <p className="card-text">
                                An Android application used to store and manage
                                watched movies, save and change details of movie, 
                                rate movies and see ratings of a movie.
                            </p>
                            <p className="card-languages">
                                <span className="languages-sub">Languages: </span> 
                                <span className="language">Java-100.0%</span>
                            </p>
                            
                        </li>

                        <li className="card" >
                            <h3 className="card-heading">
                                EzReload
                            </h3>
                            <button 
                            onClick={()=> window.open("https://github.com/AlexPeiris7/EzReload", "_blank")}
                            className="github-button">
                                GitHub
                            </button>
                            <p className="card-text">
                                An Android application used to reload money
                                in your phone with as single click. Supports
                                Sri Lankan service providers such as Dialog, Hutch, Mobitel and Airtel.
                            </p>
                            <p className="card-languages">
                                <span className="languages-sub">Languages: </span> 
                                <span className="language">Java-100.0%</span>
                            </p>
                            
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default ProjectCards;
