import React from 'react';
import './about.css';
import myImage from "../images/myImage.jpg"
import Resume from "../resume/alexio_cv.pdf"

function About(){
    return(
        <div id='about-section' className="about-container">
            <div className="about-inner-container">
                <h1 className="about-me-heading">About Me</h1>
                <div className="about-me-description">
                    <p>I am a 20 years old Software Engineer Undergraduate from Informatics Institute
                    of Technology who is seeking to find the opportunity to work in a fun and
                    challenging working environment to gain valuable experience,
                    knowledge and to improve necessary
                    skills to become a fully-fledged Software Engineer.</p>
                    <a
                    className="resume-button"
                    href = {Resume} 
                    target = "_blank"
                    >
                    Resume
                    </a>
                </div>
                <img className="about-me-image" src={myImage} />
            </div>
        </div>
    );
}

export default About;